.code-sprite-wrapper {
    padding: 0 5px;
    height: 60px;
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    background-color: #efefef;
    position: relative;
    --bg: url("data:image/jpeg;base64,");
    box-sizing: border-box;
    --box-width: 25px;
    width: auto;
}
.code-sprite {
    display: inline-block;
}
.code-sprite-wrapper:hover {
    cursor: pointer;
    background-color: #f1f1f1;
}
.code-sprite-item {
    width: var(--box-width);
    height: 40px;
    position: relative;
    box-sizing: content-box;
    overflow: clip;
    backdrop-filter: brightness(3);
    pointer-events: none;
    user-select: none;
}
.code-sprite-item::before {
    content:"-、?·+";
    position: absolute;
    display: inline-block;
    width: 20px;
    height: 20px;
    color: #ea9c97;
    z-index: 1;
    transform: translate(-2px,-2px);
    font-size: large;
    font-weight: 600;
}
.code-sprite-item img {
    width: calc(var(--box-width) * 10);
    height: 100%;
    position: relative;
    user-focus-pointer: none;
    user-focus: none;
    user-modify: none;
}
